let data = [];

chrome.runtime?.onMessage?.addListener(function (
    request,
    sender,
    sendResponse
) {
    switch (request.action) {
        case extensionActions.sendVocabListToExtension:
            // console.log("sr-app: get vocab data from extension");
            data = request.data.length > data.length ? request.data : data;
            console.log("Received data - current data length: ", data.length);
            // console.log(request);
            sendResponse({ isSuccess: data === request.data });
            break;
        case extensionActions.getVocabData:
            console.log("sr-app: get vocab data from extension");
            console.log("current data length: ", data.length);
            // console.log(request);
            sendResponse({ data: data });
            break;
        case extensionActions.updateVocabData:
            console.log("sr-app: update vocab");
            const _update = request.payload;
            // const index =
            //     request.indexInData && request.indexInData >= 0
            //         ? request.indexInData
            //         : data.findIndex((item) => item.id === _update.id);

            const index =
                request.moveToEndOfList ||
                !(request.indexInData && request.indexInData >= 0)
                    ? data.findIndex((item) => item.id === _update.id)
                    : request.indexInData;
            console.log({ index, indexRequest: request.indexInData });
            // console.log({ index: index, _update, indexSended: request.index });
            // console.log({ _update });

            if (request.moveToEndOfList && index > -1) {
                console.log("move to the end of list");
                const newData = [
                    ...data,
                    {
                        ...data[index],
                        ..._update,
                    },
                ];

                newData.splice(index, 1);
                data = newData;
            } else if (index > -1) {
                data[index] = { ...data[index], ..._update };
            }
            // console.log("current data length: ", data.length);
            // console.log(request);
            // data = request.payload;
            // sendResponse({ data: data });
            break;
        case extensionActions.addNewVocabInData:
            console.log("sr-app: add new vocab");
            console.log(request.payload);
            // console.log(request);
            // data = request.payload;
            data.unshift(request.payload);
            console.log("current data length: ", data.length);
            // sendResponse({ data: data });
            break;
        default:
            break;
    }
    // Xử lý thông điệp từ trang web ở đây
    // if (request.action === ) {
    //   // Xử lý và trả lời thông điệp
    //   sendResponse({ data: "Dữ liệu từ tiện ích mở rộng" });
    // }
});

// on active tab

chrome.tabs?.onActivated?.addListener(function (activeInfo) {
    // activeInfo chứa thông tin về tab web đang được kích hoạt
    const tabId = activeInfo.tabId;
    // console.log({ activeInfo });
    console.log("active new tab", "sending data...");
    // Gửi tin nhắn đến tab web được kích hoạt
    chrome.tabs.sendMessage(tabId, { action: "tab_activated", data });
});

